@import url("https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@200;300;400;700&display=swap");
*,
::before,
::after {
	box-sizing: border-box;
	padding: 0;
	margin: 0;
}

body {
	background: #edf2f8;
	font-family: "Barlow Semi Condensed", sans-serif;
	line-height: 1.7;
	font-size: 1rem;
}

.testimonials {
	max-width: 80vw;
	margin: 5em auto;
	padding: 1em;
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	gap: 1em;
}

.card {
	background: #fff;
	border-radius: 0.5em;
	padding: 1em;
	box-shadow: rgba(17, 12, 46, 0.15) 0px 48px 100px 10px;
	margin-bottom: 1em;
}

.card__header {
	display: flex;
	align-items: center;
	gap: 1em;
	margin-bottom: 1em;
}

.card__heder h3 {
	font-size: 2rem;
}

.card__heder p {
	opacity: 50%;
}
.card__img {
	width: 40px;
	height: 40px;
	border-radius: 50%;
	border: 2px solid #996ed8;
}

.card__lead {
	font-size: 1.5rem;
	font-weight: 500;
	line-height: 1.3;
	margin-bottom: 0.3em;
}

.card__quote {
	font-size: 1rem;
	font-weight: 500;
	line-height: 1.4;
	opacity: 70%;
}

.card--bg-purple {
	background: hsl(263, 55%, 52%);
	color: #fff;
	background-image: url(../images/bg-pattern-quotation.svg);
	background-repeat: no-repeat;
	background-position: top 10px right 100px;
}

.card--bg-gray-blue {
	background: hsl(217, 19%, 35%);
	color: #fff;
}

.card--bg-black-blue {
	background: hsl(219, 29%, 14%);
	color: #fff;
}

.card:nth-of-type(1) {
	grid-column: 1 / 3;
}

.card:nth-of-type(4) {
	grid-column: 2 / 4;
	grid-row: 2;
}

.card:nth-of-type(5) {
	grid-column: 4;
	grid-row: 1 / 3;
}

footer {
	text-align: center;
}

@media (max-width: 760px) {
	.testimonials {
		grid-template-columns: 1fr;
		width: 100%;
	}

	.card:nth-of-type(1) {
		grid-column: 1;
	}

	.card:nth-of-type(4) {
		grid-column: 1;
		grid-row: 4;
	}

	.card:nth-of-type(5) {
		grid-column: 1;
		grid-row: 5;
	}
}
